import { Component, OnInit } from '@angular/core';
import { AppService } from '../services/app.service';
import { UserService } from '../user.service';


@Component({
  selector: 'app-videos',
  templateUrl: './videos.page.html',
  styleUrls: ['./videos.page.scss'],
})
export class VideosPage implements OnInit {

  constructor(private _AppService:AppService, private _UserService:UserService) { }

  is_user_logged = false

  data_loaded = false

  page_loaded = false

  data = []

  index = 1

  get_data(){
    this._AppService.get_data(`app_news_by_sections/5?page=${this.index}`,'r').subscribe((res:any)=>{
      
      this.data = this.data.concat(res.results.section_5)
      this.data_loaded = true
      this.index++
      console.log(this.data)
    })
  }


  loadData(_event){
    // console.log('ssss')
    this.get_data()
    _event.target.complete()
  }

  _router(url){
    this._AppService.router_navigation(url)
  }


  saving_object_with_routing(_title, _text, _date, _author,_video) {
    localStorage.setItem('title', _title)
    // localStorage.setItem('img', _img)
    localStorage.setItem('text', _text)
    localStorage.setItem('date', _date)
    localStorage.setItem('author', _author)
    localStorage.setItem('video', _video)
  }

  months_arabic = ["يناير", "فبراير", "مارس", "إبريل", "مايو", "يونيو",
  "يوليو", "أغسطس", "سبتمبر", "أكتوبر", "نوفمبر", "ديسمبر"];

transform_date(__date) {
  // __date = '2021-03-27 22:46:22'
  __date = __date.replace(/-/g, ' ');
  let _arr = __date.split(" ");
  let _generated_date = _arr
  let _final_date = `${_generated_date[2]} ${this.months_arabic[parseInt(_generated_date[1]) - 1]} - ${_generated_date[3]}`
  return _final_date
}

  ngOnInit() {
    this.is_user_logged = this._UserService.is_user_logged(false)
    this.get_data()
    // this._AppService.infinite_scroll('.infinite_box')
  }

}
